import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import Translate from 'react-translate-component';
import back from '../dataStore/images/back.svg';

const customStyles = {
	content: {
		top: '50%',
		left: '50%',
		right: 'auto',
		bottom: 'auto',
		marginRight: '-50%',
		transform: 'translate(-50%, -50%)',
		boxShadow: '0px 0px 100px -70px green, 0px 0px 100px -70px green',
		border: '2px solid rgba(0,100,0, 0.6) '
	},
	overlay: {
		backgroundColor: 'rgba(0,50,0, 0.2)'
	}
};

Modal.setAppElement('#root');

export const LoginControl = ({ dialogStatus, setDialogStatus }) => {
	const [ modalIsOpen, setModelIsOpen ] = useState(dialogStatus);
	const [ register, setRegister ] = useState(false);
	const [ firstName, setFirstName ] = useState('');
	const [ lastName, setLastName ] = useState('');
	const [ phone, setPhone ] = useState('');
	const [ email, setEmail ] = useState('');
	const [ password, setPassword ] = useState('');

	useEffect(
		() => {
			setModelIsOpen(dialogStatus);
		},
		[ dialogStatus ]
	);

	const closeModal = () => {
		setFirstName('');
		setLastName('');
		setPhone('');
		setEmail('');
		setPassword('');
		setDialogStatus(false);
		setModelIsOpen(false);
		setRegister(false);
	};

	const handleSignIn = (event) => {
		event.preventDefault();
		closeModal();
		alert(`Welcome ! ${email}`);
	};

	const handleRegisterClick = () => {
		setModelIsOpen(false);
		setRegister(true);
	};

	// back button control
	const handleBackClick = () => {
		setRegister(false);
		setModelIsOpen(true);
	};

	const handleSignUp = (event) => {
		event.preventDefault();
		closeModal();
		alert(
			`Congratulation ${firstName} ${lastName} ! your have successfully registered with email: ${email} phone: ${phone} and password: ${password}`
		);
	};

	return (
		<div>
			<Modal isOpen={modalIsOpen} onRequestClose={closeModal} style={customStyles} contentLabel="Example Modal">
				<div className="w-80 h-80 md:w-104 rounded mt-2">
					<form className="flex items-center flex-col mx-1">
						<Translate
							component="input"
							type="email"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500 "
							value={email}
							attributes={{ placeholder: 'email' }}
							onChange={(event) => setEmail(event.target.value)}
						/>

						<Translate
							component="input"
							type="email"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500 "
							value={password}
							attributes={{ placeholder: 'password' }}
							onChange={(event) => setPassword(event.target.value)}
						/>

						<Translate
							component="input"
							type="submit"
							className="w-1/3 my-3 border rounded cursor-pointer bg-green-500 text-gray-100 "
							attributes={{ value: 'signin' }}
							onClick={(event) => handleSignIn(event)}
						/>

						<Translate
							component="input"
							type="button"
							className="w-1/2 mt-8 mb-3 border rounded cursor-pointer bg-green-500 text-gray-100"
							attributes={{ value: 'register' }}
							onClick={handleRegisterClick}
						/>
					</form>
				</div>
			</Modal>

			{/* Register Model */}

			<Modal isOpen={register} onRequestClose={closeModal} style={customStyles} contentLabel="Example Modal">
				<div className="w-80 h-80 md:w-104 rounded mt-2">
					<form className="flex items-center flex-col mx-1">
						<figure onClick={handleBackClick} className="mr-auto mt-0 mb-2 cursor-pointer">
							<img src={back} alt="back btn" className="inline-block w-8 h-8" />
						</figure>
						<Translate
							component="input"
							type="text"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500"
							value={firstName}
							attributes={{ placeholder: 'firstName' }}
							onChange={(event) => setFirstName(event.target.value)}
							required
						/>

						<Translate
							component="input"
							type="text"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500"
							value={lastName}
							attributes={{ placeholder: 'lastName' }}
							onChange={(event) => setLastName(event.target.value)}
							required
						/>

						<Translate
							component="input"
							type="email"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500 "
							value={email}
							attributes={{ placeholder: 'email' }}
							onChange={(event) => setEmail(event.target.value)}
						/>

						<Translate
							component="input"
							type="phone"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500 "
							value={phone}
							attributes={{ placeholder: 'phone' }}
							onChange={(event) => setPhone(event.target.value)}
							required
						/>

						<Translate
							component="input"
							type="email"
							className="w-full my-3 border rounded text-center focus:outline-none focus:border-green-500 "
							value={password}
							attributes={{ placeholder: 'password' }}
							onChange={(event) => setPassword(event.target.value)}
						/>

						<Translate
							component="input"
							type="submit"
							className="w-1/3 my-3 border rounded cursor-pointer bg-green-500 text-gray-100 "
							attributes={{ value: 'register' }}
							onClick={(event) => handleSignUp(event)}
						/>
					</form>
				</div>
			</Modal>
		</div>
	);
};
