import React, { useEffect, useRef, useState } from 'react';
import * as d3 from 'd3';
import '../css/bower_components/style.css';
var labella = require('../../node_modules/labella');

export const Timeline = ({ timelineEvents }) => {
	const svgRef = useRef();

	let events = timelineEvents.map((item) => {
		return { date: new Date(item.date.replace(/,/g, '/')), name: item.name };
	});

	//----------------------------
	// console.log(new Date().toString('M,d,yyyy')); //datejs
	// console.log(new Date('2020,02,19'.replace(/,/g, '/'))); //regex converting , separated string date to '/' separated integers
	// const dateObj = new Date(2014, 6, 25); // js object yyyy mm dd must integer to across browsers
	// --------------------------- do not touch below

	const [ data, setData ] = useState(events);

	useEffect(
		() => {
			setData(events);
		},
		// eslint-disable-next-line
		[ data ]
	);

	useEffect(
		() => {
			var formatDate = d3.time.format('%b %e');

			var options = {
				margin: { left: 20, right: 20, top: 20, bottom: 20 },
				initialWidth: svgRef.current.offsetWidth,
				initialHeight: 220
			};

			var innerWidth = options.initialWidth - options.margin.left - options.margin.right;
			// var innerHeight = options.initialHeight - options.margin.top - options.margin.bottom;
			// var colorScale = d3.scale.category10();

			function labelText(d) {
				return formatDate(d.date) + ' - ' + d.name;
			}

			// compute labels dimension

			var timeScale = d3.time
				.scale()
				.domain(
					d3.extent(data, function(d) {
						return d.date;
					})
				)
				.range([ 0, innerWidth ])
				.nice();

			var vis = d3
				.select(svgRef.current)
				.append('svg')
				.attr('width', options.initialWidth)
				.attr('height', options.initialHeight)
				.attr('className', 'flex w-full bg-red-500')
				.append('g')
				.attr('transform', 'translate(' + options.margin.left + ',' + options.margin.top + ')');

			var dummyText = vis.append('text');

			var nodes = data.map(function(movie) {
				var bbox = dummyText.text(labelText(movie))[0][0].getBBox();
				movie.h = bbox.height;
				movie.w = bbox.width;
				return new labella.Node(timeScale(movie.date), movie.w + 9, movie);
			});

			dummyText.remove();

			// ---------------------------------------------------
			// Draw dots on the timeline
			// ---------------------------------------------------

			vis.append('line').classed('timeline', true).attr('x2', innerWidth);

			var linkLayer = vis.append('g');
			var labelLayer = vis.append('g');
			var dotLayer = vis.append('g');

			dotLayer
				.selectAll('circle.dot')
				.data(nodes)
				.enter()
				.append('circle')
				.classed('dot', true)
				.attr('r', 3)
				.attr('cx', function(d) {
					return d.getRoot().idealPos;
				});

			function color(d, i) {
				return 'rgba(0,130,0,0.9)';
			}

			//---------------------------------------------------
			// Labella has utility to help rendering
			//---------------------------------------------------
			var renderer = new labella.Renderer({
				layerGap: 60,
				nodeHeight: nodes[0].data.h,
				direction: 'bottom'
			});

			function draw(nodes) {
				// Add x,y,dx,dy to node
				renderer.layout(nodes);

				// Draw label rectangles
				var sEnter = labelLayer
					.selectAll('rect.flag')
					.data(nodes)
					.enter()
					.append('g')
					.attr('transform', function(d) {
						return 'translate(' + (d.x - d.width / 2) + ',' + d.y + ')';
					});

				sEnter
					.append('rect')
					.classed('flag', true)
					.attr('width', function(d) {
						return d.data.w + 9;
					})
					.attr('height', function(d) {
						return d.data.h + 4;
					})
					.attr('rx', 2)
					.attr('ry', 2)
					.style('fill', color);

				sEnter.append('text').attr('x', 4).attr('y', 15).style('fill', '#fff').text(function(d) {
					return labelText(d.data);
				});

				// Draw path from point on the timeline to the label rectangle
				linkLayer
					.selectAll('path.link')
					.data(nodes)
					.enter()
					.append('path')
					.classed('link', true)
					.attr('d', function(d) {
						return renderer.generatePath(d);
					})
					.style('stroke', color)
					.style('stroke-width', 2)
					.style('opacity', 0.6)
					.style('fill', 'none');
			}

			//---------------------------------------------------
			// Use labella.Force to place the labels
			//---------------------------------------------------

			var force = new labella.Force({
				minPos: -10,
				maxPos: innerWidth
			})
				.nodes(nodes)
				.compute();

			draw(force.nodes());

			// end of hook callback
		},
		// eslint-disable-next-line
		[ data ]
	);

	return (
		<div className="chart-container w-full mt-2">
			<div ref={svgRef} id="timeline" className="" />
		</div>
	);
};
