import React, { useState } from 'react';
import { Navbar } from './Navbar.component';
import { Timeline } from './Timeline.component';
import Modal from 'react-modal';
import Translate from 'react-translate-component';
import preval from 'preval.macro';

const uuidv1 = require('uuid/v1');

Modal.setAppElement('#root');
const customStyles = {
	content: {
		top: '50%',
		left: '50%',
		right: 'auto',
		bottom: 'auto',
		marginRight: '-50%',
		transform: 'translate(-50%, -50%)',
		boxShadow: '0px 0px 100px -70px green, 0px 0px 100px -70px green',
		border: '2px solid rgba(0,100,0, 0.6) '
	},
	overlay: {
		backgroundColor: 'rgba(0,50,0, 0.2)'
	}
};

const totalCards = preval` 
	const fs = require('fs');
	const files = fs.readdirSync('src/dataStore/cards');
	module.exports = (files.length);
	`;

const allCardData = preval`
	const fs = require('fs')
	const allCards = fs.readdirSync('src/dataStore/cards');
	let allCardPath = allCards.map(cardName => {
		let cardPath = [];
		cardPath.push('../dataStore/cards/'+cardName+'/')
		return cardPath;
	})

	const languagesAvailable = fs.readdirSync('src/dataStore/login')
	let allCardData = allCardPath.map(cardPath => {
		return languagesAvailable.map(languageFile => {
			return JSON.parse(fs.readFileSync(require.resolve(cardPath+languageFile, 'utf8')))
		})
	})

	module.exports = allCardData
	`;

const loginData = preval`
	const fs = require('fs')
	const allFiles = fs.readdirSync('src/dataStore/login')
	let loginData = allFiles.map(item => {
		return JSON.parse(fs.readFileSync(require.resolve('../dataStore/login/'+item), 'utf8'))
	})

	module.exports = loginData
  `;

const Homepage = () => {
	const [ cardModalStatus, setCardModelStatus ] = useState(false);
	const [ timelineEvents, setTimelineEvents ] = useState(null);
	const [ cardNo, setCardNo ] = useState(null);
	const [ langSelected, setLangSelected ] = useState({ value: 'english', label: 'English' });
	const [ data ] = useState({ loginData: loginData, allCardData: allCardData });

	const filterDataByLanguage = (language) => {
		let languageContent = [];
		data.allCardData.map((card, i) => {
			return card.map((objItem) => {
				if (objItem.language === language) {
					languageContent.push(objItem);
				}
				return true;
			});
		});
		const login = [];
		data.loginData.map((item) => {
			if (item.language === language) {
				login.push(item);
			}
			return true;
		});

		var loginObj = Object.assign({}, ...login);
		var cards = { ...loginObj };
		languageContent.map((item, i) => {
			Object.defineProperty(cards, `card${i + 1}`, {
				value: item,
				writable: true,
				enumerable: true,
				configurable: true
			});
			return true;
		});

		return cards;
	};

	const [ englishData ] = useState(filterDataByLanguage('english'));
	const [ hindiData ] = useState(filterDataByLanguage('hindi'));
	const [ bengaliData ] = useState(filterDataByLanguage('bengali'));

	const closeModal = () => {
		setCardModelStatus(false);
	};

	const logContent = () => {
		console.log(englishData.card1.heading);
	};

	const handleCardClick = (cardNo) => {
		setCardNo(cardNo);
		setCardModelStatus(true);
		const languageObjects = [ englishData, hindiData, bengaliData ];
		if (langSelected.value === 'english') {
			const englishCardData = languageObjects[0]['card' + cardNo].events;
			setTimelineEvents(englishCardData);
		} else if (langSelected.value === 'hindi') {
			const englishCardData = languageObjects[1]['card' + cardNo].events;
			setTimelineEvents(englishCardData);
		} else {
			const englishCardData = languageObjects[2]['card' + cardNo].events;
			setTimelineEvents(englishCardData);
		}
	};

	const Card = ({ cardNo }) => {
		return (
			<div className="flex justify-center mx-8 ls:mx-autor">
				<div
					onClick={() => handleCardClick(cardNo)}
					className=" p-1 mt-4 ls:w-9/12 text-justify border rounded cursor-pointer"
				>
					{/* translate is similar to div */}

					<Translate content={`card${cardNo}.heading`} component="h1" className="font-semibold text-center" />
					<hr />
					<Translate content={`card${cardNo}.content`} component="p" />
				</div>
			</div>
		);
	};

	return (
		<div>
			<header>
				<Navbar
					englishData={englishData}
					hindiData={hindiData}
					bengaliData={bengaliData}
					setLangSelected={setLangSelected}
					logContent={logContent}
				/>
				<hr />
			</header>

			{/* CARDS on the homepage */}

			<main className="max-w-screen-xl m-auto mt-4 ">
				{Array.from(Array(totalCards), (item, i) => <Card key={uuidv1()} cardNo={i + 1} />)}
			</main>

			{/* MODAL for card */}
			<Modal
				isOpen={cardModalStatus}
				onRequestClose={closeModal}
				style={customStyles}
				contentLabel="Example Modal"
			>
				<div className="flex w-screen85 h-screen73 max-w-screen-lg justify-center text-center rounded mt-2">
					<div className="w-full">
						<div className="flex h-5/12 border ">
							<div className="w-full  ">
								{/* <div>Heading</div> */}
								<Translate
									content={`card${cardNo}.cardModelHeading`}
									component="h1"
									className="font-semibold text-center"
								/>
								<hr />
								<div className="flex justify-center ">
									{/* <p>Model Content</p> */}
									<Translate content={`card${cardNo}.cardModelContent`} component="p" />
								</div>
							</div>
							<div className="border w-16">Media Icons</div>
						</div>
						<div className=" w-full flex justify-center items-center">
							<Timeline timelineEvents={timelineEvents} />
						</div>
					</div>
				</div>
			</Modal>
		</div>
	);
};

export default Homepage;
